<template>
  <div
    class="template-product__container component-section critical-component-clear"
    :class="{
      [$class('loading')]:
        !getComponentActive('main-product') || state === 'loading',
    }"
  >
    <div class="template-product__main">
      <div class="row container">
        <div class="template-product__header">
          <div class="col xs12">
            <div class="template-product__breadcrumb">
              <breadcrumb
                v-if="enableBreadcrumbs"
                :additional-path="breadcrumb"
                :page-title="product.title"
              />
            </div>
          </div>
        </div>
      </div>
      <div class="row container">
        <product-form-header
          block-class="mobile-only"
          :liquid="liquid"
          :product="product"
          :variant="variant"
          :missing-variant="unmatchedVariant"
        />
      </div>
      <div class="row container">
        <div class="col xs12 m6 l7 xl8">
          <div class="template-product__gallery">
            <product-gallery
              :color="color"
              :images="images"
              :media="media"
              :template="template"
              :state="state"
              :selected-variant="variant"
            />
          </div>
        </div>

        <div class="col xs12 m6 l5 xl4 container">
          <div class="template-product__aside">
            <div class="main-product__form">
              <product-form
                :liquid="liquid"
                :product="product"
                :variant="variant"
                :reviews="reviews"
                @update-variant="handleVariantUpdate"
                @update-product="handleProductUpdate"
                @variant-check="checkMatchingVariant"
              />
            </div>
          </div>
        </div>
        <div
          class="main-product__description rte is-hidden"
          v-html="product.description || liquid.description"
        />
      </div>
    </div>
    <div
      class="main-product__description rte is-hidden"
      v-html="product.description || liquid.description"
    />
  </div>
</template>

<script>

/**
 * Vue: Main product
 * -----------------------------------------------------------------------------
 * Main product page component.
 *
 * @param {String} breadcrumb - Additional breadcrumb path.
 * @param {String} handle - Product page's original handle.
 * @param {String} enableBreadcrumbs- Check if breadcrumbs are enabled.
 * @param {String} liquid.description - Product's Liquid description.
 * @param {Boolean} liquid.hasOnlyDefaultVariant - Has only default variant.
 * @param {String} liquid.price - Product's Liquid price.
 * @param {String} liquid.sectionId - Section's Liquid ID.
 * @param {String} liquid.title - Product's Liquid title.
 * @param {Object|Boolean} product - Canvas formatted product object.
 * @param {Object|Boolean} variant - Canvas formatted variant object.
 *
 */
import ProductForm from '~async/product-form/product-form'
import ProductFormHeader from '~async/product-form/product-form-header'
import ProductGallery from '~async/product-gallery/product-gallery'
import Breadcrumb from '~global/breadcrumb/breadcrumb.vue'

import product from '~/graphql/queries/product.gql'
import { formatProduct } from '~/helpers/format'

export default {
  name: 'MainProduct',

  components: {
    Breadcrumb,
    ProductForm,
    ProductGallery,
    ProductFormHeader,
  },

  props: {
    breadcrumb: {
      type: [Boolean, Object],
      default: false,
    },
    handle: {
      type: String,
      default: cnvs.page.handle,
    },
    enableBreadcrumbs: {
      type: Boolean,
      default: false,
    },
    liquid: {
      type: Object,
      default: () => ({
        description: '',
        hasOnlyDefaultVariant: false,
        price: 0,
        sectionId: '',
        title: '',
        allowPreOrder: false,
      }),
    },
    product: {
      type: [Object, Boolean],
      default: false,
    },
    media: {
      type: [Array],
      default: () => ([]),
    },
    variant: {
      type: [Object, Boolean],
      default: () => ({}),
    },
    template: {
      type: String,
      default: '',
    },
    reviews: {
      type: String,
      default: () => (''),
    },
  },

  emits: ['update-request-object'],

  data() {
    return {
      currentHandle: cnvs.page.handle,
      previousProductGalleryImages: [],
      state: 'loading',
      images: null,
      color: null,
      bundles: {
        ...window.cnvs.productBundles,
      },
      unmatchedVariant: false,
    }
  },

  computed: {

    /**
     * Compute product card badge tag.
     * - Only one badge at a time. If multiple tags exist only the first tag
     * is displayed, and the tag badge: is prioritised over badge_pink:
     * @returns {String|Boolean}
     */
    badgeTag() {
      if (!this.product?.tags) {
        return false
      }

      const badgeTags = ['badge:', 'badge_pink:']

      const tag = this.product.tags.find((productTags) =>
        badgeTags.find((tag) => productTags.indexOf(tag) > -1),
      )

      return tag || false
    },

    /**
     * Compute badge modifiers.
     * @returns {String|Boolean}
     */
    badgeModifiers() {
      if (!this.badgeTag) {
        return false
      }

      const matches = this.badgeTag?.toLowerCase().match(/badge_(.+):/)

      return matches && matches[1]
    },

    /**
     * Compute the promo sale message.
     * @returns {String|Null}
     */
    promoSaleMessage() {
      const messages = []

      const { collectionPromos } = this.metafields
      const isThemeMessageEnabled = this.$variable("sale.enablePDPSaleMessage")
      const themeMessage = this.$variable("sale.pdpSaleMessaging")

      const productTagPresent =
        this.product?.tags?.indexOf("discount:not-eligible") > -1

      if (productTagPresent) {
        return null
      }

      // If theme message is enabled and theme message is set
      if (isThemeMessageEnabled && themeMessage) {
        messages.push(themeMessage)
      }

      if (collectionPromos) {
        for (let i = 0; i < collectionPromos.length; i++) {
          const { message, priority } = collectionPromos[i]

          if (priority === "true") {
            messages.length = 0
            messages.push(message)
            break
          }

          messages.push(message)
        }
      }

      // If array contains collection messages, remove theme message
      if (messages.length > 1 && isThemeMessageEnabled && themeMessage) {
        messages.shift()
      }

      return messages.length ? messages.join("\n") : ""
    },

    /**
     * Compute if product type gift is true or false.
     * @return {boolean}
     */
    isGiftProduct() {
      return this.product.giftCard
    },
  },

  watch: {

    /**
     * Watch currentHandle for update to load new product.
     */
    currentHandle() {
      this.queryProduct(this.currentHandle)
    },

    product() {
      this.images = this.productGalleryImages()
    },

    variant() {
      this.color = this.selectedColor()
    },
  },

  mounted() {
    this.queryProduct()
  },

  methods: {

    /**
     * Loads product from GraphQL.
     * @param {String} [handle] - Product handle to load.
     */
    async queryProduct(handle = this.handle) {
      if (handle === false) {
        return
      }

      if (this.product.handle === handle && !this.product.liquid) {
        this.state = 'ready'
        return
      }

      try {
        const response = await cnvs.Query({
          locale: cnvs.store.language,
          query: product,
          variables: {
            country: cnvs.store.country,
            handle,
          },
        })

        if (!response.product) {
          response.product = this.liquid
        }

        if (cnvs.settings.disableComponentReadyState) {
          return
        }
        this.$emit('update-request-object', {
          response: formatProduct(response),
          type: 'product',
        })

        this.state = 'ready'
      } catch (error) {
        reportError('Failed to load product', error)
      }
    },

    /**
     * Handle variant update from product form.
     * @param {Object} variant - Selected variant.
     */
    handleVariantUpdate(variant) {
      this.$emit('update-request-object', {
        response: variant,
        type: 'variant',
      })

      const url = variant?.id
        ? `${location.origin}${location.pathname}?variant=${variant.id}`
        : `${location.origin}${location.pathname}`

      history.replaceState({
        html: '',
      }, '', url)
    },

    handleProductUpdate(product) {
      this.$emit('update-request-object', {
        response: product,
        type: 'product',
      })

      const url = product.variants[0]?.id
        ? `${product.url}?variant=${product.variants[0].id}`
        : `${product.url}`

      history.replaceState({
        html: '',
      }, '', url)
    },

    /**
     * Compute product gallery images.
     * - Filters gallery images that match current color
     * @returns {Array}
     */
    productGalleryImages() {
      const filteredImages = this.product?.images || []

      if (!this.color || !this.product) {
        return filteredImages.filter((image) => {
          const alt = image.alt?.toLowerCase() || ""
          return !alt.includes("swatch") && !alt.includes("visualizer")
        })
      }

      return filteredImages.length > 0 ? filteredImages : this.images
    },

    /**
     * Compute currently selected color.
     * @returns {String|Boolean}
     */
    selectedColor() {
      if (!this.variant && !this.variant?.selectedOptions) {
        return false
      }

      const colorOption = this.variant?.selectedOptions.find(
        (option) => option.name?.toLowerCase() === "color",
      )

      return colorOption ? colorOption.value?.toLowerCase() : false
    },

    checkMatchingVariant(variant) {
      this.unmatchedVariant = variant
    },
  },
}
</script>

<style lang="scss">
@import './main-product';
</style>
